import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import bgImg from './bg.jpg';
import onImg from './on.png';
import offImg from './off.png';
import backImg from './back.png';

import bgm from './bgm.mp3';

class Settings extends Component {
  constructor(props) {
    super(props);
    this.cancelMute = this.cancelMute.bind(this);
    this.mute = this.mute.bind(this);
  }

  cancelMute() {
    this.audio.muted = false;
  }

  mute() {
    this.audio.muted = true;
  }

  render() {
    return (
      <div className="bg" style={styles.bg}>
        <audio
          ref={(audio) => { this.audio = audio; }}
          controls
          autoplay
          style={{
            position: 'absolute',
            bottom: 0,
            left: '30%',
          }}
        >
          <source src={bgm} />
        </audio>
        {/* <div style={{  }}> */}
        <div style={styles.imgWrapper}>
          <a onClick={this.cancelMute}>
            <img src={onImg} style={styles.img} />
          </a>
        </div>
        <div style={styles.imgWrapper}>
          <a onClick={this.mute}>
            <img src={offImg} style={styles.img} />
          </a>
        </div>
        {/* </div> */}
        <Link to="/">
          <img src={backImg}
            className="back-btn"
          />
        </Link>
      </div>
    );
  }
}

   
const styles = {
  bg: {
    backgroundImage: `url(${bgImg})`,
  },
  imgWrapper: {
    display: 'inline-block',
    width: '50%',
    height: '100%',
  },
  img: {
    display: 'block',
    width: '100px',
    height: '100px',
    margin: '40% auto 40% auto',
  },
};

export default Settings;
    
