import React, { Component } from 'react';
import { BrowserRouter, Switch, Route, Link } from 'react-router-dom';

import bgImg from './bg.jpg';

import jungleBtn from './buttons/jungle.png';
import snowBtn from './buttons/snow.png';
import oceanBtn from './buttons/ocean.png';
import volcanoBtn from './buttons/volcano.png';

import jungleSmall from './small/jungle.png';
import snowSmall from './small/snow.png';
import oceanSmall from './small/ocean.png';
import volcanoSmall from './small/volcano.png';

import jungleMp4 from './display/jungleV.mp4';
import jungleWebm from './display/jungleV.webm';
import snowMp4 from './display/snowV.mp4';
import snowWebm from './display/snowV.webm';
import oceanBig from './display/ocean.jpg';
import volcanoBig from './display/volcano.jpg';

import backImg from './back.png';

class MapChoosing extends Component {
  render() {
    return (
      <div className="bg" style={styles.bg}>
        <BrowserRouter
          basename="/map-choosing"
        >
          <div style={{ width: '100%', height: '100%' }}>
            <div style={{
              // ...styles.imgWrapper,
              // width: '35%',
              // padding: '100px 0 100px 100px',
              position: 'absolute',
              top: '40%',
              left: '15%',
            }}>
              <Link to="/jungle">
                <img src={jungleBtn} className="small-btn"
                  style={{ position: 'relative', left: 22 }}
                />
              </Link>
              <br />
              <Link to="/snow">
                <img src={snowBtn} className="small-btn"
                  style={{ position: 'relative', left: 30 }}
                />
              </Link>
              <br />
              <Link to="/ocean">
                <img src={oceanBtn} className="small-btn"
                  style={{ position: 'relative', left: 38 }}
                />
              </Link>
              <br />
              <Link to="/volcano">
                <img src={volcanoBtn} className="small-btn"
                  style={{ position: 'relative', left: 50 }}
                />
              </Link>
            </div>
            <div style={{
              // ...styles.imgWrapper, width: '65%'
              width: '50%',
              position: 'absolute',
              top: '40%',
              right: 5,
            }}>
              <Switch>
                <Route path="/jungle" render={() =>
                  <Link to="/jungle-display">
                    <img src={jungleSmall} style={styles.banner} />
                  </Link>
                } />
                <Route path="/snow" render={() =>
                  <Link to="/snow-display">
                    <img src={snowSmall} style={styles.banner} />
                  </Link>
                } />
                <Route path="/ocean" render={() =>
                  <Link to="/ocean-display" replace>
                    <img src={oceanSmall} style={styles.banner} />
                  </Link>
                } />
                <Route path="/volcano" render={() =>
                  <Link to="/volcano-display">
                    <img src={volcanoSmall} style={styles.banner} />
                  </Link>
                } />
              </Switch>

            </div>
            <Switch>
              <Route path="/jungle-display" render={() => (
                <video style={styles.fullScreen} autoPlay controls>
                  <source src={jungleWebm} type="video/webm" />
                  <source src={jungleMp4} type="video/mp4" />
                </video>
              )} />
              <Route path="/snow-display" render={() => (
                <video style={styles.fullScreen} autoPlay controls>
                  <source src={snowWebm} type="video/webm" />
                  <source src={snowMp4} type="video/mp4" />
                </video>
              )} />
              <Route path="/ocean-display" render={() => (
                <img src={oceanBig} style={styles.fullScreen} />
              )} />
              <Route path="/volcano-display" render={() => (
                <img src={volcanoBig} style={styles.fullScreen} />
              )} />
            </Switch>

          </div>

        </BrowserRouter>

        <Link to="/">
          <img src={backImg}
            className="back-btn"
          />
        </Link>
      </div>

    );
  }
}

const styles = {
  bg: {
    backgroundImage: `url(${bgImg})`,
  },
  imgWrapper: {
    display: 'inline-block',
    height: '100%',
  },
  banner: {
    width: '80%',
    float: 'right',
  },
  fullScreen: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    zIndex: 100,
  },
};

export default MapChoosing;