import React from 'react';
import { Link } from 'react-router-dom';

import bgImg from './bg.jpg';
import startImg from './start.png';
import settingsImg from './settings.png';

export default () => (
  <div className="bg" style={styles.bg}>
    <div style={{ paddingTop: 200, paddingLeft: 200, }}>
      <Link to="/map-choosing/jungle">
        <img src={startImg} className="btn" />
      </Link>
      <br />
      <Link to="/settings">
        <img src={settingsImg} className="btn" />
      </Link>
    </div>
  </div>
  
);

const styles = {
  bg: {
    backgroundImage: `url(${bgImg})`,
    // backgroundSize: '100%',
  },
  
};
